<script>
import NavMenu from './NavMenu'
import Footer from './Footer'

export default {
    name: 'Parentpagege',
    components: {
    NavMenu,
    Footer,
    },
    data: () => ({
      images: [
        //   {src: require("../assets/img/parentpage/1.png"), speed: '-5'},
        //    {src: require("../assets/img/parentpage/2.png"), speed: '5'},
        //    {src: require("../assets/img/parentpage/3.png"), speed: '2'},
        //    {src: require("../assets/img/parentpage/4.png"), speed: '6'},
        //    {src: require("../assets/img/parentpage/5.png"), speed: '8'},
        //    {src: require("../assets/img/parentpage/6.png"), speed: '-2'},
        //    {src: require("../assets/img/parentpage/7.png"), speed: '4'},
        //    {src: require("../assets/img/parentpage/8.png"), speed: '-1'},
        //    {src: require("../assets/img/parentpage/9.png"), speed: '6'},
        //    {src: require("../assets/img/parentpage/10.png"), speed: '-7'},
        //    {src: require("../assets/img/parentpage/11.png"), speed: '-5'},
        //    {src: require("../assets/img/parentpage/12.png"), speed: '2'},
           
      ] ,
    }),
    mounted() {
            document.addEventListener('mousemove', this.movie)
    },
    methods: {
        movie(e) {
            document.querySelectorAll('.layer').forEach(layer => {
                const speed = layer.getAttribute('data-speed')
                const x = (window.innerWidth - e.pageX * speed) / 100
                const y = (window.innerHeight - e.pageY * speed) / 100

                layer.style.transform = `translateX(${x}px) translateY(${y}px)`
            })

        }
    }
}
</script>